// Custom scripts go here
// This is hacky and needs to be done in PHP

ready(function() {
  var navTabs = document.getElementsByClassName('tab-content');
  if(navTabs != null) {
    for(i = 0; i < navTabs.length; i ++) {
      var tabPane = navTabs[i].getElementsByClassName('tab-pane')[0];
      tabPane.classList.add('active');
    }
  }
});